import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';

@Component({
  selector: 'app-constact-detail',
  templateUrl: './constact-detail.component.html',
  styleUrls: ['./constact-detail.component.css']
})
export class ConstactDetailComponent implements OnInit {

  name = '';
  phoneNumber = '';

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.paramMap.subscribe((params: ParamMap) => {
      const id = params.get('id');
      this.name = params.get('name');
      this.phoneNumber = params.get('phoneNumber');
      // console.log(id, name, phoneNumber);
    });
  }

}



