import { Component, OnInit } from '@angular/core';
import { WeatherService } from './weather.service';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers: [WeatherService]
})
export class WeatherComponent implements OnInit {

  cityName = 'Saigon';
  temp = '';
  isLoading = false;

  constructor(private weatherSrv: WeatherService) { }

  ngOnInit() {
    // this.weatherSrv.getTemp(this.cityName).
    //   then(res => console.log(res))
    //   .catch(err => console.log(err));
  }

  getWeather() {
    this.isLoading = true;
    this.weatherSrv.getTemp(this.cityName).
    then(res => this.parseRes(res))
      .catch(err => {
        console.log(err);
        this.temp = err;
      });
  }

  parseRes(res) {
    this.isLoading = false;
    console.log(res);
    if(res.cod === 200) {
      this.temp = res.main.temp;
    } else {
      this.temp = res.statusText;
    }
  }

  getMsg() {
    return this.isLoading ? 'Loading...' : this.cityName + ' is now ' + this.temp;
  }

}



