import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { ChaComponent } from './components/cha/cha.component';
import { ConComponent } from './components/con/con.component';
import { Con1Component } from './components/con1/con1.component';
import { PersonComponent } from './components/person/person.component';
import { ListComponent } from './components/list/list.component';
import { ViewChildChaComponent } from './components/viewchild/cha/cha.component';
import { ViewChildCon1Component } from './components/viewchild/con1/con1.component';
import { Con2Component } from './components/viewchild/con2/con2.component';
import { NgcontentComponent } from './components/ngcontent/ngcontent.component';
import { FormatComponent } from './components/format/format.component';
import { CustomformatComponent } from './components/customformat/customformat.component';
import { RoundPipe } from './components/round/round.pipe';
import { HttpserviceComponent } from './components/httpservice/httpservice.component';
import { HttpServer } from './components/httpservice/http.server';
import { WeatherComponent } from './components/weather/weather.component';
import { SigninComponent } from './components/signin/signin.component';
import { SigninserverComponent } from './components/signinserver/signinserver.component';
import { SigninserverServer } from './components/signinserver/signinserver.server';
import { SignupserverComponent } from './components/signupserver/signupserver.component';
import { AppRoutingModule } from './app.routing.module';
import { ContactsModule } from './contacts.module';

@NgModule({
  declarations: [
    AppComponent,
    ChaComponent,
    ConComponent,
    Con1Component,
    PersonComponent,
    ListComponent,
    ViewChildChaComponent,
    ViewChildCon1Component,
    Con2Component,
    NgcontentComponent,
    FormatComponent,
    CustomformatComponent,
    RoundPipe,
    HttpserviceComponent,
    WeatherComponent,
    SigninComponent,
    SigninserverComponent,
    SignupserverComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    ReactiveFormsModule,
    AppRoutingModule,
    ContactsModule
  ],
  providers: [HttpServer, SigninserverServer],
  bootstrap: [AppComponent]
})
export class AppModule { }



