import { Component, OnInit } from '@angular/core';
import { SigninserverServer } from './signinserver.server';

@Component({
  selector: 'app-signinserver',
  templateUrl: './signinserver.component.html',
  styleUrls: ['./signinserver.component.css']
})
export class SigninserverComponent implements OnInit {

  constructor(private signInServer: SigninserverServer) { }

  onSubmit(formSignIn) {
    this.signInServer.sendPost(formSignIn.value)
      .then(result => console.log(result))
      .catch(err => console.log(err));
  }

  ngOnInit() {
  }

}



