import { Component, OnInit, ViewChild } from '@angular/core';
import { ViewChildCon1Component} from '../con1/con1.component';
import { Con2Component} from '../con2/con2.component';

@Component({
  selector: 'view-child-app-cha',
  templateUrl: './cha.component.html',
  styleUrls: ['./cha.component.css']
})
export class ViewChildChaComponent implements OnInit {

  @ViewChild(ViewChildCon1Component)
  @ViewChild(Con2Component)
  myCon: ViewChildCon1Component;
  myCon2: Con2Component;

  tang(con) {
    con.value = con.value + 1;
  }

  tang2() {
    this.myCon2.value++;
  }

  constructor() { }

  ngOnInit() {
  }

}



