import { Component, OnInit } from '@angular/core';
import {FormControl, FormGroup, FormBuilder, Validators} from '@angular/forms';

@Component({
  selector: 'app-signupserver',
  templateUrl: './signupserver.component.html',
  styleUrls: ['./signupserver.component.css']
})
export class SignupserverComponent implements OnInit {

  formSignUp: FormGroup;

  constructor(private fb: FormBuilder) {}

  onSubmit(formSignUp) {
    console.log(this.formSignUp.value);
  }

  ngOnInit() {
    // thu gon
    this.formSignUp = this.fb.group({
      email: ['', [this.gmailValidator, Validators.email]],
      password: '',
      subject: this.fb.group({
        nodejs: true,
        angularjs: false,
        reactjs: true
      })
    });

    // basic
    // this.formSignUp = new FormGroup({
    //   email: new FormControl('ngo@gmail.com'),
    //   password: new FormControl(),
    //   subject: new FormGroup({
    //     nodejs: new FormControl(true),
    //     angularjs: new FormControl(false),
    //     reactjs: new FormControl(true)
    //   })
    // });
  }

  gmailValidator(formControl: FormControl) {
    return formControl.value.includes('@gmail.com') ? null : { gmail: true};
  }

}



